      },
      expandedKeys: ['01', '02', '03', '04', '05', '06', '6-3'],
      contentMap: {
        '01': '本次测试项目设计扫雷游戏。',
        '02': ` <p>扫雷是一款经典的大众类的益智小游戏，游戏目标是在最短的时间内根据点击格子出现的数字找出所有非雷格子，同时避免踩雷。分成以下三个任务：</p>
                <p>1、 阶段一：LCD12864驱动设计（60分钟）；</p>
                <p>2、 阶段二：基本游戏功能（120分钟）；</p>
                <p>3、 阶段三：综合游戏设计（180分钟）。</p>`,
        '05': '扫雷是一款经典的大众类的益智小游戏，游戏目标是在最短的时间内根据点击格子出现的数字找出所有非雷格子，同时避免踩雷，下面对游戏的一些概念进行解释说明。',
        '5-1': `<p> 周边：指坐标点周边3x3的区域；</p>
                <p> 地雷点：点阵屏上显示为红色，有地雷的点称作地雷点</p>
                <p> 危险点：点阵屏上显示为蓝色，周边有地雷的点称作危险点，危险点可以获取周边3x3的区域中地雷数量</p>
                <p> 安全点：点阵屏上显示为绿色，周边没有地雷的点称作安全点</p>
                <p> 标记点：点阵屏上显示为紫色，玩家标记的点称为标记点</p>`,
        '5-2': `<p> 光标移动：通过拨动摇杆控制光标移动</p>
                <p> 标记地雷：SW16控制打开/关闭标记操作，使能标记时，单击摇杆按键，光标所在点被标记，如果该点已经标记，则取消标记。</p>
                <p> 单击打开：在不使能标记时，单击摇杆按键，对于非标记点执行打开操作</p>
                <p> 双击：在已经打开的危险点上双击，周边标记点的数量等于危险点周边地雷数量，那么打开周边未标记的点</p>`,
        '5-3': 'CCCCCCCCCCCC',
        '6-1': `<p>本次任务使用串行接口驱动LCD12864，请打开文件《lcd12864.c》和《lcd12864.h》补全相关函数。</p>
                <p>阶段 1.1</p>
                <p>串行模式下的接口时序图如下图所示。 串行数据传送共分三个字节完成：</p>
                <p>第一字节：串口控制—格式 11111AB0 （A 为数据传送方向控制：H 表示数据从 LCD 到 MCU，L 表示数据从 MCU 到 LCD； B 为数据类型选择：H 表示数据是显示数据，L 表示数据是控制指令）</p>
                <p>第二字节：(并行)8 位数据的高 4 位—格式 DDDD0000</p>
                <p>第三字节：(并行)8 位数据的低 4 位—格式 DDDD0000</p>
                <p>详细说明可以参考ST7920的器件手册第26页。</p>
                <p>请完成函数void sendFrame(uint32_t data)，向LCD12864发送一组数据，data是要发送的一组数据，低24bit有效，从高位开始发送。</p>
                <p>阶段1.2</p>
                <p>请实现向LCD12864写控制命令和写数据的函数，这些函数需要调用阶段1.1的函数sendFrame:</p>
                <p> void writeCmd(uint8_t cByte)，向LCD12864发送控制命令，cByte是控制命令字节；</p>
                <p> void writeData(uint8_t dByte) ，向LCD12864发送数据，dByte是要发送的数据字节；</p>
                <p>在《user_function.c》的setup函数中已经写了LCD的测试用例，如果你的答案正确，可以在LCD屏幕上看到“LCD12864工作正常”的字样。</p>`,
        '6-1-1': '串行模式下的接口时序图如下图所示。 串行数据传送共分三个字节完成：',
        '6-3-1': '<p>在LCD的第二行显示“游戏时长: MM:SS ”,MM为分钟数，SS为秒钟数，保留前置0，时间为当前局游戏的持续时间。</p>',
      },
      fileList: [],
      nodeList: [
        { key: '01', title: '简介' },
        { key: '02', title: '任务描述' },
        { key: '03', title: '编程环境' },
        { key: '04', title: '接口表', children: [] },
        {
          key: '05',
          title: '游戏说明',
          children: [
            { key: '5-1', title: '定义说明' },
            { key: '5-2', title: '操作说明' },
            { key: '5-3', title: '游戏裁决', isLeaf: true },
          ],
        },
        {
          key: '06',
          title: '编程任务',
          children: [
            { key: '6-1', title: '第一阶段编程任务' },
            { key: '6-2', title: '第二阶段编程任务' },
            {
              key: '6-3',
              title: '第三阶段编程任务',
              children: [
                { key: '6-3-1', title: '任务 3.1：游戏计时', children: [] },
                { key: '6-3-2', title: '任务 3.2：光标功能', children: [] },
                { key: '6-3-3', title: '任务 3.3：调试功能', children: [] },
              ],
            },
          ],
        },
      ],
      timer: null,
    }
  },
  methods: {
    onCateChange() {},
    onSubmit() {},
    onDragEnter() {},
    onDrop(info) {
      console.log(info)
      const dropKey = info.node.eventKey
      const dragKey = info.dragNode.eventKey
      const dropPos = info.node.pos.split('-')
      const dropPosition = info.dropPosition - Number(dropPos[dropPos.length - 1])
      const loop = (data, key, callback) => {
        data.forEach((item, index, arr) => {
          if (item.key === key) {
            return callback(item, index, arr)
          }
          if (item.children) {
            return loop(item.children, key, callback)
          }
        })
      }
      const data = [...this.gData]

      // Find dragObject
      let dragObj
      loop(data, dragKey, (item, index, arr) => {
        arr.splice(index, 1)
        dragObj = item
      })
      if (!info.dropToGap) {
        // Drop on the content
        loop(data, dropKey, (item) => {
          item.children = item.children || []
          // where to insert 示例添加到尾部，可以是随意位置
          item.children.push(dragObj)
        })
      } else if (
        (info.node.children || []).length > 0 && // Has children
        info.node.expanded && // Is expanded
        dropPosition === 1 // On the bottom gap
      ) {
        loop(data, dropKey, (item) => {
          item.children = item.children || []
          // where to insert 示例添加到尾部，可以是随意位置
          item.children.unshift(dragObj)
        })
      } else {
        let ar
        let i
        loop(data, dropKey, (item, index, arr) => {
          ar = arr
          i = index
        })
        if (dropPosition === -1) {
          ar.splice(i, 0, dragObj)
        } else {
          ar.splice(i + 1, 0, dragObj)
        }
      }
      this.nodeList = data
    },
    onEditorClick(e, editor) {
      console.log('Element clicked')
      console.log(e)
      console.log(editor)
    },
    onSelect(keys, data) {
      console.log('onSelect', keys, data.node.eventKey)
      const node = data.node
      this.content = this.contentMap[node.eventKey]
    },
    onContextMenu(e) {
      for (const i in e.path) {
        if (e.path[i].classList && e.path[i].classList.contains('ant-tree-node-content-wrapper')) {
          let rect = this.$refs.content.getBoundingClientRect()
          this.selectedNodeText = e.path[i].innerText
          console.log(e)
          this.$refs.nodeMenu.style.left = `${e.x - rect.left}px`
          this.$refs.nodeMenu.style.top = `${e.y - rect.top}px`
          this.$refs.nodeMenu.style.display = 'block'
          break
        }
      }
    },
    onMouseOut(e) {
      this.timer = setTimeout(() => {
        this.$refs.nodeMenu.style.display = 'none'
      }, 300)
    },
    onMouseOver(e) {
      if (this.timer) {
        clearTimeout(this.timer)
        this.timer = null
      }
      this.$refs.nodeMenu.style.display = 'block'
    },
    toggleFullscreen() {
      this.fullscreen.toggle(this.$refs.content)
    },

    handleUploadChange(info) {
      const status = info.file.status
      if (status !== 'uploading') {
        console.log(info.file, info.fileList)
      }
      if (status === 'done') {
        this.$message.success(`${info.file.name} file uploaded successfully.`)
      } else if (status === 'error') {
        this.$message.error(`${info.file.name} file upload failed.`)
      }
      this.fileList = info.fileList
    },
    handlePreview() {},
  },
}
</script>

